@import "../../vars.css";

.button {
  background: none;
  outline: none;
  border: 1px solid rgba(216, 216, 216, 0.2);
  color: white;
  text-align: left;
  padding: 12px 28px;
  display: flex;
  align-items: center;
}

.button:hover {
  background: var(--dark-grey);
  cursor: pointer;
}
